<template>
  <div class="login-container">
    <h3 class="title">用户登录</h3>
    <el-form ref="ruleFormRef" :model="form" class="login-form" :rules="rules">
      <el-form-item prop="username">
        <el-input prefix-icon="user" v-model="form.username"/>
      </el-form-item>
      <el-form-item prop="password">
        <el-input prefix-icon="lock" v-model="form.password" show-password/>
      </el-form-item>
      <el-button type="primary" @click="submitForm()">用户登录</el-button>
    </el-form>
  </div>
</template>

<script setup>
import {reactive, ref} from "vue";
import {useStore} from 'vuex'

const store = useStore()

const form = reactive({
  username: 'admin',
  password: '123456'
})
// 账号密码规则校验
const rules = reactive({
  username: [
    {required: true, message: '请输入用户名...', trigger: 'blur'},
    {min: 3, max: 5, message: '用户名为3到5位...', trigger: 'blur'},
  ],
  password: [
    {required: true, message: '请输入密码...', trigger: 'blur'},
    {min: 4, max: 6, message: '密码为4到6位...', trigger: 'blur'},
  ]
})

// 登录验证
const ruleFormRef = ref(null)
const submitForm = () => {
  ruleFormRef.value.validate((valid, fields) => {
    if (valid) {
      store.dispatch('login', form)
    } else {
      console.log(fields)
    }
  })
}
</script>

<style lang="scss" scoped>
$color-back: #2d2d2d;
.login-container {
  width: 30%;
  height: 30vh;
  margin: 35vh 35%;
  background-color: rgba(45, 45, 45, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 0px;
  border-radius: 10px;
  box-shadow: 5px 5px 5x $color-back;

  h3 {
    margin-bottom: 20px;
    font-size: 24px;
  }

  .login-form {
    display: flex;
    flex-direction: column;
    justify-content: center;

  }
}
</style>
